  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Onest', sans-serif;
}

img {
  display: block;
}

/* utils */
.flex {
  display: flex;
}

.just-between {
  justify-content: space-between;
}

.just-left {
  justify-content: start;
}

.items-center {
  align-items: center;
}

.col {
  flex-direction: column;
}

.row {
  flex-direction: row;
}

.gap-1 {
  gap: 0.3rem;
}

.gap-2 {
  gap: 0.5rem;
}

.gap-3 {
  gap: 0.8rem;
}

.gap-4 {
  gap: 1rem;
}

/* ----- */
.container {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  padding-left: 16px;
  padding-right: 16px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo a {
  text-decoration: none;
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 16px;
}

.logo img {
  border-radius: 100%;
}

.logo span {
  font-weight: 800;
  font-size: 32px;
  color: #252526;
}

header {
  padding: 24px 16px;
}

nav #hamburger {
  background-color: transparent;
  border: none;
}

nav .hamburger {
  display: block;
  width: 48px;
  padding: 4px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
}

nav ul {
  display: none;
}

nav ul li {
  list-style: none;
}

nav ul li a {
  text-decoration: none;
  padding: 16px;
  display: inline-block;
  color: #5b5c5e;
  font-size: 24px;
}

nav ul li:hover a {
  color: #141414;
}

.text-orange {
  color: #ff8000 !important;
}

.font-semibold {
  font-weight: 600;
}

.hero {
  height: 400px;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0px 16px;
}

.hero::before {
  content: "";
  background-color: #141414;
  opacity: 0.5;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero .tagline {
  color: #ffffff;
  font-size: 48px;
  z-index: 2;
}

/* detail */

.hero-detail {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-width: 100vw;
  overflow: hidden;
  text-align: left;
}

.hero-detail img {
  width: 100%;
  height: 400px;
  object-fit: cover;
}

.detail-name {
  font-size: 32px;
}

.rating-detail {
  display: flex;
  align-items: center;
}

.category-list li {
  list-style: none;
  font-size: 12px;
  padding: 4px 8px;
  border-radius: 32px;
  border: 1px solid #ff5e00;
  color: #ff8000;
}

.info .rating::before {
  content: "";
  display: inline-block;
  font-size: 12px;
}

.info .label {
  border-left: 2px solid #ff5e00;
  padding-left: 8px;
}

.menus {
  margin-top: 24px;
  justify-content: center;
}

.menus h3 {
  margin-bottom: 8px;
  font-size: 24px;
}

.menus .foods,
.menus .drinks {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 32px;
  border-radius: 24px;
  width: 100%;
  border: 1px solid #ff8000;
}

.menus .foods li,
.menus .drinks li {
  list-style: none;
}

.menus .foods span,
.menus .drinks span {
  font-weight: 900;
  font-size: 24px;
  color: #ff8000;
  opacity: .3;
}

.customer-reviews h2 {
  text-align: center;
  font-size: 32px;
  margin: 24px 0px;
}

.reviews-wrapper {
  display: flex;
  justify-content: start;
  gap: 24px;
  overflow: auto;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
}

.reviews-wrapper .review {
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  flex-shrink: 0;
  align-items: center;
  width: 400px;
  padding: 24px;
  border-radius: 16px;
  box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);
  -webkit-box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);
  -moz-box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);
}

.reviews-wrapper .review .name {
  font-weight: 500;
  font-size: 24px;
}

.reviews-wrapper .review .date {
  font-weight: 300;
  font-size: 12px;
}

.reviews-wrapper li {
  list-style: none;
}

/* ------- */

.restos {
  text-align: center;
}

.restos h2 {
  font-size: 32px;
  margin: 24px 0 24px;
}

.resto-cards {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 32px;
  text-align: left;
}

.resto-item {
  border-start-start-radius: 12px;
  overflow: hidden;
}

.resto-item a {
  text-decoration: none;
  color: #141414;
}

.thumbnail-item {
  position: relative;
}

.thumbnail-item .label {
  position: absolute;
  top: 24px;
  z-index: 2;
  padding: 8px 16px 8px 8px;
  background: #ff8000;
  color: #ffffff;
  border-end-end-radius: 50px;
  border-start-end-radius: 50px;
  font-size: 24px;
}

.resto-item img {
  width: 100%;
  height: 250px;
  position: relative;
}

.resto-item .desc .title {
  font-size: 24px;
}

.rating {
  display: inline-block;
  margin-top: 8px;
}

.rating,
.rating-detail {
  background-color: #ffeec2;
  color: #ff5e00;
  padding: 4px 8px;
  border-radius: 8px;
}

.rating-detail {
  margin: 0px;
  display: flex;
  align-items: center;
  gap: 2px;
}

footer {
  text-align: center;
  margin-top: 40px !important;
  padding: 40px;
}

.showNav {
  display: flex;
  flex-direction: column;
  gap: 6px;
  background: #ffffff;
  padding: 24px;
  border-radius: 24px;
  z-index: 10;
  text-align: right;
  position: absolute;
  right: 16px;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #ff8000;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ff8000;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}

/* add reviews */
.add-reviews {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 24px 0px;
}

.input-reviews {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 24px;
}

.input-reviews input,
.input-reviews button {
  padding: 16px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);
  -webkit-box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);
  -moz-box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);
}

.input-reviews button {
  background-color: #ff8000;
  font-size: 18px;
  color: #ffffff;
  cursor: pointer;
}

/* ----------- */

/* custom loader */
.loader-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.custom-loader {
  width: 50px;
  height: 24px;
  background:
    radial-gradient(circle closest-side, #ff8000 90%, #0000) 0% 50%,
    radial-gradient(circle closest-side, #ff8000 90%, #0000) 50% 50%,
    radial-gradient(circle closest-side, #ff8000 90%, #0000) 100% 50%;
  background-size: calc(100%/3) 12px;
  background-repeat: no-repeat;
  animation: d3 1s infinite linear;
}

/* --------- */

@keyframes d3 {
  20% {
    background-position: 0% 0%, 50% 50%, 100% 50%
  }

  40% {
    background-position: 0% 100%, 50% 0%, 100% 50%
  }

  60% {
    background-position: 0% 50%, 50% 100%, 100% 0%
  }

  80% {
    background-position: 0% 50%, 50% 50%, 100% 100%
  }
}

@media screen and (min-width: 768px) {
  .resto-cards {
    grid-template-columns: repeat(2, 1fr);
  }

  .hero {
    max-width: 100%;
  }

  .menus {
    flex-direction: row;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1200px;
  }

  .resto-cards {
    grid-template-columns: repeat(3, 1fr);
  }

  nav #hamburger {
    display: none;
  }

  nav ul {
    display: flex;
    gap: 16px;
  }

  nav ul li a {
    font-size: 16px;
  }

  .hero {
    width: 1200px;
  }

  .menus {
    gap: 24px;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;EACtB,gCAAgC;AAClC;;AAEA;EACE,cAAc;AAChB;;AAEA,UAAU;AACV;EACE,aAAa;AACf;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,SAAS;AACX;;AAEA,UAAU;AACV;EACE,WAAW;EACX,cAAc;EACd,kBAAkB;EAClB,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;AACrB;;AAEA;EACE,qBAAqB;EACrB,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,cAAc;AAChB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,6BAA6B;EAC7B,YAAY;AACd;;AAEA;EACE,cAAc;EACd,WAAW;EACX,YAAY;EACZ,yBAAyB;EACzB,kBAAkB;AACpB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,qBAAqB;EACrB,aAAa;EACb,qBAAqB;EACrB,cAAc;EACd,eAAe;AACjB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,WAAW;EACX,cAAc;EACd,gBAAgB;EAChB,yDAAgE;EAChE,sBAAsB;EACtB,4BAA4B;EAC5B,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,kBAAkB;EAClB,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,yBAAyB;EACzB,YAAY;EACZ,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,WAAW;EACX,YAAY;AACd;;AAEA;EACE,cAAc;EACd,eAAe;EACf,UAAU;AACZ;;AAEA,WAAW;;AAEX;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,gBAAgB;EAChB,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,aAAa;EACb,iBAAiB;AACnB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,gBAAgB;EAChB,mBAAmB;EACnB,yBAAyB;EACzB,cAAc;AAChB;;AAEA;EACE,YAAY;EACZ,qBAAqB;EACrB,eAAe;AACjB;;AAEA;EACE,8BAA8B;EAC9B,iBAAiB;AACnB;;AAEA;EACE,gBAAgB;EAChB,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,eAAe;AACjB;;AAEA;;EAEE,aAAa;EACb,sBAAsB;EACtB,QAAQ;EACR,aAAa;EACb,mBAAmB;EACnB,WAAW;EACX,yBAAyB;AAC3B;;AAEA;;EAEE,gBAAgB;AAClB;;AAEA;;EAEE,gBAAgB;EAChB,eAAe;EACf,cAAc;EACd,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,cAAc;EACd,kBA