und-size: cover;\n  background-repeat: no-repeat;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  position: relative;\n  padding: 0px 16px;\n}\n\n.hero::before {\n  content: \"\";\n  background-color: #141414;\n  opacity: 0.5;\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n}\n\n.hero .tagline {\n  color: #ffffff;\n  font-size: 48px;\n  z-index: 2;\n}\n\n/* detail */\n\n.hero-detail {\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n  max-width: 100vw;\n  overflow: hidden;\n  text-align: left;\n}\n\n.hero-detail img {\n  width: 100%;\n  height: 400px;\n  object-fit: cover;\n}\n\n.detail-name {\n  font-size: 32px;\n}\n\n.rating-detail {\n  display: flex;\n  align-items: center;\n}\n\n.category-list li {\n  list-style: none;\n  font-size: 12px;\n  padding: 4px 8px;\n  border-radius: 32px;\n  border: 1px solid #ff5e00;\n  color: #ff8000;\n}\n\n.info .rating::before {\n  content: \"\";\n  display: inline-block;\n  font-size: 12px;\n}\n\n.info .label {\n  border-left: 2px solid #ff5e00;\n  padding-left: 8px;\n}\n\n.menus {\n  margin-top: 24px;\n  justify-content: center;\n}\n\n.menus h3 {\n  margin-bottom: 8px;\n  font-size: 24px;\n}\n\n.menus .foods,\n.menus .drinks {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n  padding: 32px;\n  border-radius: 24px;\n  width: 100%;\n  border: 1px solid #ff8000;\n}\n\n.menus .foods li,\n.menus .drinks li {\n  list-style: none;\n}\n\n.menus .foods span,\n.menus .drinks span {\n  font-weight: 900;\n  font-size: 24px;\n  color: #ff8000;\n  opacity: .3;\n}\n\n.customer-reviews h2 {\n  text-align: center;\n  font-size: 32px;\n  margin: 24px 0px;\n}\n\n.reviews-wrapper {\n  display: flex;\n  justify-content: start;\n  gap: 24px;\n  overflow: auto;\n  overflow-x: scroll;\n  -webkit-overflow-scrolling: touch;\n}\n\n.reviews-wrapper .review {\n  display: flex;\n  text-align: center;\n  flex-direction: column;\n  justify-content: center;\n  flex-shrink: 0;\n  align-items: center;\n  width: 400px;\n  padding: 24px;\n  border-radius: 16px;\n  box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);\n  -webkit-box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);\n  -moz-box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);\n}\n\n.reviews-wrapper .review .name {\n  font-weight: 500;\n  font-size: 24px;\n}\n\n.reviews-wrapper .review .date {\n  font-weight: 300;\n  font-size: 12px;\n}\n\n.reviews-wrapper li {\n  list-style: none;\n}\n\n/* ------- */\n\n.restos {\n  text-align: center;\n}\n\n.restos h2 {\n  font-size: 32px;\n  margin: 24px 0 24px;\n}\n\n.resto-cards {\n  width: 100%;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 32px;\n  text-align: left;\n}\n\n.resto-item {\n  border-start-start-radius: 12px;\n  overflow: hidden;\n}\n\n.resto-item a {\n  text-decoration: none;\n  color: #141414;\n}\n\n.thumbnail-item {\n  position: relative;\n}\n\n.thumbnail-item .label {\n  position: absolute;\n  top: 24px;\n  z-index: 2;\n  padding: 8px 16px 8px 8px;\n  background: #ff8000;\n  color: #ffffff;\n  border-end-end-radius: 50px;\n  border-start-end-radius: 50px;\n  font-size: 24px;\n}\n\n.resto-item img {\n  width: 100%;\n  height: 250px;\n  position: relative;\n}\n\n.resto-item .desc .title {\n  font-size: 24px;\n}\n\n.rating {\n  display: inline-block;\n  margin-top: 8px;\n}\n\n.rating,\n.rating-detail {\n  background-color: #ffeec2;\n  color: #ff5e00;\n  padding: 4px 8px;\n  border-radius: 8px;\n}\n\n.rating-detail {\n  margin: 0px;\n  display: flex;\n  align-items: center;\n  gap: 2px;\n}\n\nfooter {\n  text-align: center;\n  margin-top: 40px !important;\n  padding: 40px;\n}\n\n.showNav {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n  background: #ffffff;\n  padding: 24px;\n  border-radius: 24px;\n  z-index: 10;\n  text-align: right;\n  position: absolute;\n  right: 16px;\n}\n\n.skip-link {\n  position: absolute;\n  top: -40px;\n  left: 0;\n  background-color: #ff8000;\n  color: white;\n  padding: 8px;\n  z-index: 100;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #ff8000;\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n/* add reviews */\n.add-reviews {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  margin: 24px 0px;\n}\n\n.input-reviews {\n  width: 100%;\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n  margin-top: 24px;\n}\n\n.input-reviews input,\n.input-reviews button {\n  padding: 16px;\n  border: 1px solid #e0e0e0;\n  border-radius: 8px;\n  box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);\n  -webkit-box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);\n  -moz-box-shadow: 8px 1px 48px -9px rgba(0, 0, 0, 0.06);\n}\n\n.input-reviews button {\n  background-color: #ff8000;\n  font-size: 18px;\n  color: #ffffff;\n  cursor: pointer;\n}\n\n/* ----------- */\n\n/* custom loader */\n.loader-wrapper {\n  width: 100%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.custom-loader {\n  width: 50px;\n  height: 24px;\n  background:\n    radial-gradient(circle closest-side, #ff8000 90%, #0000) 0% 50%,\n    radial-gradient(circle closest-side, #ff8000 90%, #0000) 50% 50%,\n    radial-gradient(circle closest-side, #ff8000 90%, #0000) 100% 50%;\n  background-size: calc(100%/3) 12px;\n  background-repeat: no-repeat;\n  animation: d3 1s infinite linear;\n}\n\n/* --------- */\n\n@keyframes d3 {\n  20% {\n    background-position: 0% 0%, 50% 50%, 100% 50%\n  }\n\n  40% {\n    background-position: 0% 100%, 50% 0%, 100% 50%\n  }\n\n  60% {\n    background-position: 0% 50%, 50% 100%, 100% 0%\n  }\n\n  80% {\n    background-position: 0% 50%, 50% 50%, 100% 100%\n  }\n}\n\n@media screen and (min-width: 768px) {\n  .resto-cards {\n    grid-template-columns: repeat(2, 1fr);\n  }\n\n  .hero {\n    max-width: 100%;\n  }\n\n  .menus {\n    flex-direction: row;\n  }\n}\n\n@media screen and (min-width: 1024px) {\n  .container {\n    max-width: 1200px;\n  }\n\n  .resto-cards {\n    grid-template-columns: repeat(3, 1fr);\n  }\n\n  nav #hamburger {\n    display: none;\n  }\n\n  nav ul {\n    display: flex;\n    gap: 16px;\n  }\n\n  nav ul li a {\n    font-size: 16px;\n  }\n\n  .hero {\n    width: 1200px;\n  }\n\n  .menus {\n    gap: 24px;\n  }\n}"],"sourceRoot":""}]);
